.App {
  text-align: center;
  font-family: arial;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.Menu {
  width: 500px;
  height: 500px;
  border-radius: 10px;
  background-color: lightseagreen;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Menu input,
button {
  width: 300px;
  height: 50px;
  margin: 5px;
  border: none;
  border-radius: 5px;
  font-size: 20px;
}

button:hover {
  cursor: pointer;
  background-color: blanchedalmond;
}

.Quiz {
  width: 500px;
  height: 500px;
  border-radius: 10px;
  background-color: lightseagreen;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Menu,
.Quiz,
.EndScreen h1 {
  color: white;
}

.EndScreen {
  width: 500px;
  height: 500px;
  border-radius: 10px;
  background-color: lightseagreen;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#nextQuestion {
  width: 200px;
}

h3 {
  color: #ffffff;
}
.chosen {
  background-color: lightpink;
}
.qbtn:hover {
  background-color: lightpink;
}
.btn1 {
  width: 200px;
}
#h2,
#p {
  align-self: flex-start;
  margin-left: 6rem;
  margin-bottom: 0.3rem;
}
#p {
  color: red;
  font-size: 1.5rem;
}
.EndScreen p {
  font-size: 2rem;
  padding: 0;
  margin: 0;
  color: white;
}
@media only screen and (max-width: 600px) {
  .Menu,
  .Quiz,
  .EndScreen {
    width: 90vw;
  }
}
